import React, { useState, useRef, useEffect } from "react";
import { TextField } from "@mui/material";
import { addDays, format, parseISO } from "date-fns";

import { useStorageState } from "./stateHelpers/customHooks";
import {
    defaultSettings,
    toggleCheckbox,
} from "./stateHelpers/defaultSettings";

import "./App.scss";

function WeekDays() {
    return (
        <div className="weekdays">
            <div>
                <p>Sunday</p>
            </div>
            <div>
                <p>Monday</p>
            </div>
            <div>
                <p>Tuesday</p>
            </div>
            <div>
                <p>Wednesday</p>
            </div>
            <div>
                <p>Thursday</p>
            </div>
            <div>
                <p>Friday</p>
            </div>
            <div>
                <p>Saturday</p>
            </div>
        </div>
    );
}

function CalCell({ cellValue, setCellValue, date }) {
    const [isEditing, setIsEditing] = useState(false);
    const inputRef = useRef();
    console.log({ date });

    useEffect(() => {
        inputRef.current?.focus();
    }, [inputRef.current]);

    const setTextInputRef = (element) => {
        inputRef.current = element;
    };

    const startEditing = (e) => {
        if (e?.target?.id === "checkbox") {
            return;
        }
        setIsEditing(true);
    };

    const setCellText = (text, checkboxValue) => {
        setCellValue({ checkboxValue, text });
    };

    const handleCheckboxClick = (currCheckboxValue) => {
        console.log({ currCheckboxValue });
        toggleCheckbox(
            (checkboxValue) => setCellValue({ ...cellValue, checkboxValue }),
            currCheckboxValue
        );
    };

    return (
        <div
            className="cal-cell"
            onClick={startEditing}
            onKeyDown={(e) => {
                if (e.key === "Enter") {
                    startEditing();
                }
                if (e.key === "Escape") {
                    setIsEditing(false);
                }
            }}
            tabIndex="0"
        >
            <div className="cell-date">{format(date, "MMM d")}</div>

            {!isEditing && <p>{cellValue.text}</p>}
            {isEditing && (
                <TextField
                    value={cellValue.text}
                    onChange={(e) =>
                        setCellText(e.target.value, cellValue.checkboxValue)
                    }
                    onBlur={() => setIsEditing(false)}
                    inputRef={setTextInputRef}
                    autoFocus
                    multiline
                />
            )}
            <div
                id="checkbox"
                className="cell-checkbox"
                onClick={() => handleCheckboxClick(cellValue.checkboxValue)}
            >
                {cellValue.checkboxValue}
            </div>
        </div>
    );
}

function CalRow({ weekNumber, sundayDate }) {
    const [weekState, setWeekState] = useStorageState(
        defaultSettings[`week-${weekNumber}`] || {},
        `week-${weekNumber}`
    );

    return (
        <div className="cal-row">
            {Array.from({ length: 7 }, (_, i) => {
                const cellValue = weekState[`day-${i}`];
                return (
                    <CalCell
                        key={i}
                        cellValue={cellValue || ""}
                        setCellValue={(value) => {
                            setWeekState({ ...weekState, [`day-${i}`]: value });
                        }}
                        date={addDays(sundayDate, i)}
                    />
                );
            })}
        </div>
    );
}

function CalPage({ title, numWeeks, monthNumber, initialDate }) {
    console.log({ initialDate });

    return (
        <div
            className="cal-page"
            style={{ gridTemplateRows: `120px 40px ${numWeeks * 100}px` }}
        >
            <header className="cal-header">
                <h1>{title}</h1>
            </header>
            <WeekDays />
            <div
                className="cal-body"
                style={{ gridTemplateRows: `repeat(${numWeeks}, 1fr)` }}
            >
                {Array.from({ length: numWeeks }, (_, i) => {
                    return (
                        <CalRow
                            key={i}
                            weekNumber={i + 5 * monthNumber}
                            sundayDate={addDays(initialDate, 7 * i)}
                        />
                    );
                })}
            </div>
        </div>
    );
}

function App() {
    const [totalWeeks, setTotalWeeks] = useStorageState(
        defaultSettings.totalWeeks,
        "totalWeeks"
    );
    const numMonths = Math.ceil(totalWeeks / 5);

    const [startDate, setStartDate] = useStorageState(
        defaultSettings.startDate,
        "startDate"
    );

    console.log({ startDate });

    return (
        <div className="App">
            <div className="settings">
                <h1>Settings</h1>
                <TextField
                    label="Number of weeks"
                    value={totalWeeks}
                    onChange={({ target: { value } }) => {
                        setTotalWeeks(value);
                    }}
                    inputProps={{ inputMode: "numeric", pattern: "[0-9]*" }}
                />
                {/* todo: set start date */}
            </div>
            {Array.from({ length: numMonths }, (_, i) => {
                const remainingWeeks = totalWeeks - i * 5;
                return (
                    <CalPage
                        key={i}
                        title="Mitch's Marathon Method"
                        numWeeks={Math.min(remainingWeeks, 5)}
                        monthNumber={i}
                        initialDate={addDays(parseISO(startDate), i * 7 * 5)}
                    />
                );
            })}
        </div>
    );
}

export default App;
